@import "../constants/colors.scss";

.advantages {
  &__list {
    width: 100%;
  }

  &__item {
    min-width: 284px;
    min-height: 167px;
    text-align: center;
    display: flex;
    flex-direction: column;
    // align-items: center;
  }

  &__item--another-bcg-color {
    background-color: $advantages-grey;
  }

  &__item-img {
    background-color: rgb(237, 191, 180);
  }

  // &__item-wrapper--border-bottom {
  //   min-height: inherit;
  //   width: 284px;
  //   padding: 24px 26px 24px 26px;
  //   border-bottom: 1px solid rgb(214, 214, 214);
  //   box-sizing: border-box;
  // }

  &__item-text {
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;
    text-transform: uppercase;
  }

  &__item-wrapper {
    width: 100%;
  }

  @media screen and (min-width: $tablet) {
    &__list {
      display: flex;
      flex-direction: column;
      width: 100%;
    }
    // &__list {
    //   display: grid;
    //   grid-template-areas:
    //     "adv1 adv1 adv1"
    //     "adv1 adv1 adv1"
    //     "adv2 adv2 adv2"
    //     "adv3 adv4 adv5";
    // }

    // .adv1 {
    //   grid-area: adv1;
    // }

    // .adv2 {
    //   grid-area: adv2;
    // }

    // .adv3 {
    //   grid-area: adv3;
    // }

    // .adv4 {
    //   grid-area: adv4;
    // }

    // .adv5 {
    //   grid-area: adv5;
    // }

    .true-city,
    .devils-bridge {
      display: flex;
      flex-wrap: wrap;
    }

    &__item-wrapper {
      width: 100%;
    }

    &__text-wrapper {
      padding: 52px 33px 52px 33px;
      flex-grow: 1;
    }

    &__item--another-bcg-color {
      display: flex;
      flex-direction: row;
    }


    .benefits {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;

      &__item {
        box-sizing: border-box;
        min-width: 257px;
        min-height: 331px;
        padding: 158px 33px 60px 33px;
        outline: 1px solid red;
      }

      &__text-wrapper {
        width: 190px;
      }
    }
  }
}

.advantages__title {
  color: rgba(51, 51, 51, 1);
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  text-align: center;
  padding: 0 18px 16px 18px;
}

.true-city__wrapper {
  background-color: rgba(129, 179, 210, 1);
  color: rgb(255, 255, 255);
  text-align: center;
  padding: 54px 18px 40px 18px;
}

.advantages__item-title--number-one::after {
  content: "— №1 —";
  display: block;
  padding: 11px 0 17px 0;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
}

.devils-bridge__wrapper {
  background-color: rgba(129, 179, 210, 1);
  color: rgb(255, 255, 255);
  text-align: center;
  padding: 54px 18px 40px 18px;
}
