@charset "UTF-8";
@media screen and (min-width: 320px) {
  .main-header {
    display: flex;
    position: relative;
    justify-content: flex-end;
    min-height: 56px;
  }
  .main-header__logo {
    display: inline-block;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 5;
  }
}
@media screen and (min-width: 768px) {
  .main-header {
    display: flex;
    justify-content: flex-start;
  }
}

@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans.woff2");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2");
  font-display: swap;
}
* {
  font-family: "PT Sans", sans-serif;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.main-nav__toggle {
  border: none;
  padding: 0;
  background-color: initial;
  padding-right: 20px;
  padding-top: 17px;
}
.main-nav__wrapper {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
.main-nav__menu-item {
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  text-align: center;
  background-color: #81b3d2;
  text-transform: uppercase;
  min-height: 55px;
  box-shadow: 0 1px rgba(255, 255, 255, 0.2);
  padding-top: 17px;
  box-sizing: border-box;
  position: relative;
}
.main-nav__menu-item .close-button {
  position: absolute;
  right: 16px;
  top: 12px;
  border: none;
  background-color: inherit;
}
@media screen and (min-width: 768px) {
  .main-nav .menu-link {
    color: black;
  }
  .main-nav__toggle {
    display: none;
  }
  .main-nav__menu-list {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }
  .main-nav__menu-item {
    font-size: 14px;
    font-weight: 700;
    line-height: 21px;
    text-align: center;
    text-transform: uppercase;
    padding-top: 17px;
    box-sizing: border-box;
    background-color: inherit;
    min-width: 153px;
  }
  .main-nav__menu-item:nth-child(2) {
    margin-right: 50px;
  }
  .main-nav__menu-item:nth-child(3) {
    margin-left: 50px;
  }
  .main-nav__wrapper {
    display: flex;
    width: 100%;
    justify-content: space-around;
  }
  .main-nav .close-button {
    display: none;
  }
}

.menu-link {
  color: white;
}

